<template>
  <div>
    <div>{{result}}</div>
    <button @click="sendData">async</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'async',
  data(){
    return {
      text: '삼겹살',
      result: ''
    }
  },
  methods: {
    sendData(){
      axios
        .get('https://openapi.naver.com/v1/search/blog', this.text, {
          headers: {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Methods": "GET, POST, PATCH, PUT, DELETE, OPTIONS",
            "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token"
            },
        })
        .then((res) => {
          console.log(res.data);
          this.result = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }
}
</script>